import { Link } from '../routes'

const Home = () => {
    const divStyle = {
        textAlign : "center",
        display : "flex"
    }
    return <div style={divStyle}>
        <Link route="/about">Go to about page</Link>
        <Link route="/contact">Go to contact page</Link>
        <Link route="/user">Go to user List</Link>
    </div>
}

export default Home